/**
 * EasyZoom core styles
 */
.easyzoom {
	position: relative;

	/* 'Shrink-wrap' the element */
	// display: inline-block;
	*display: inline;
	*zoom: 1;
}

.easyzoom img {
	vertical-align: bottom;
}

.easyzoom.is-loading img {
	cursor: progress;
}

.easyzoom.is-ready img {
	cursor: crosshair;
}

.easyzoom.is-error  img {
	cursor: not-allowed;
}

.easyzoom-notice {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 10;
	height: 32px;
	line-height: 32px;
	text-align: center;
	width: 32px;
	margin-top: -16px;
	margin-left: -16px;
	background: url(../img/ajax-loader-1.gif) no-repeat center center #fff;
	@include border-radius(2px);
	.is-error &{
		background: #fff;
	}
}

.easyzoom-flyout {
	position:absolute;
	overflow: hidden;
	background: #FFF;
	z-index: 4;
	img{
		max-width: none;
		width: auto;
	}
}

/**
 * EasyZoom layout variations
 */
.easyzoom--overlay .easyzoom-flyout {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.easyzoom--adjacent .easyzoom-flyout {
	top: 0;
	left: 100%;
	width: 100%;
	height: 100%;
	margin-left: 20px;
}



