.checkout-step {
  border-bottom: 1px solid $gray-border-color;
  padding:0 15px;
  .step-title {
    // cursor: pointer;
    margin-bottom: 0;
    padding: rem(15px) 0;
  }
  .content {
    padding-bottom: rem(10px);
  }
  .not-allowed {
    cursor: not-allowed;
    opacity: 0.5;
  }
  .content,
  .done,
  .step-edit {
    display: none;
  }
  &.-current {
    .content {
      display: block;
    }
  }
  &.-current.-reachable.-complete {
    .done,
    .step-edit {
      display: none;
    }
    .step-number {
      display: inline-block;
    }
    .content {
      display: block;
    }
  }
  &.-reachable {
    .step-title {
      cursor: pointer;
    }
  }
  &.-reachable.-complete {
    .done {
      display: inline-block;
    }
    .step-number {
      display: none;
    }
    .step-edit {
      display: block;
    }
    .content {
      display: none;
    }
  }
  .default-input {
    min-width: 40%;
    &[name=address1],
    &[name=address2] {
      min-width: 60%;
    }
  }
  .radio-field {
    margin-top: rem(30px);
    label {
      display: inline;
    }
  }
  .checkbox-field div {
    margin-top: rem(60px);
  }
  .checkbox-field + .checkbox-field div {
    margin-top: 0;
  }
  .select-field div {
    background: $gray-lighter;
    padding: rem(10px) rem(50px);
  }
  .form-footer {
  }
  .payment-options {
    label {
      margin-bottom: 0;
    }
    .payment-option {
      margin-bottom: rem(8px);
    }
  }
  .step-number {
    display: inline-block;
    margin-right: rem(6px);
  }
  .address-item {
    &.selected {
      border-color: $link-color;
    }
  }
  .address-alias {
    display: inline-block;
    font-weight: bold;
    margin-bottom: rem(10px);
  }
  .address {
  }
  .custom-radio {
    input[type="radio"] {
      height: rem(20px);
      width: rem(20px);
    }
  }
  #delivery-addresses,
  #invoice-addresses {
    margin-top: rem(20px);
  }
  .add-address {
    margin-top: rem(20px);
  }
  .delivery-option {
    padding: rem(15px) 0;
    margin-bottom: rem(15px);
  }
  .carrier-delay,
  .carrier-name {
    display: inline-block;
    word-break: break-word;
    text-align: left;
  }
