.search_widget_block{
  #st_header &{
    width: 280px;
  }
  #st_header #mobile_bar &{
    width: auto;
    margin-left: 10px;
    margin-right: 10px;
    // min-width: 160px;
  }
  .search_widget{
    position: relative;
  }
}
.search_results{
  position: absolute;
  left: 0;
  right: 0;
  z-index: 8000;
  background: $white;
  box-shadow: 0 0px 4px rgba(0, 0, 0, 0.1);
  .st-menu &{
    box-shadow: none;
  }
  .display_results_static &{
    position: static;
  }
  .autocomplete-suggestion{
    padding: rem(6px);
    #side_search &{
      padding-left: 0;
      padding-right: 0;
    }
    &:hover{
      background: $base-background,
    }
  }
  .search_more_products{
    display: block;
    text-align: center;
    padding: rem(12px) rem(6px);
  }
  .search_product_img{
    width: 46px;
    margin-right: rem(6px);
  }
  .search_product_row{
  }
} 
.search_product_name span{font-weight:bold;}
.search_result_item{display: none;}
.search_show_img .search_product_img{display:block;}
.search_show_name .search_product_name{display:block;}
.search_show_price .search_product_price{display:block;}

