body,
html {
  height: 100%;
}
body {
  font-family: Arial,Helvetica,sans-serif;
  font-size: $base-font-size;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  color: $base-text-color;
  -ms-overflow-style: scrollbar;
}
img{
  max-width: 100%;
  height: auto;
}
ul {
  list-style: none;
  list-style-position: inside;
  padding-left: 0;
  &.list-style-circle{list-style-type: circle;}
  &.list-style-disc{list-style-type: disc;}
  &.list-style-square{list-style-type: square;}
  &.list-style-circle, &.list-style-disc, &.list-style-square{
    li{
      margin-bottom: 2px;
    }
  }
  &.nested {
    margin-left: 16px;
  }
}
.style_content{
  ul {
    &.list-style-circle{list-style-type: circle;}
    &.list-style-disc{list-style-type: disc;}
    &.list-style-square{list-style-type: square;}
  }
}
a, .link_color,.close{
  color: $link-color;
}
a, a.link_color, button.link_color,.close{
  &:hover {
    color: $link-hover-color;
    text-decoration: none;
  }
  &:focus{
    -moz-outline-style: none;
  }
  &.go, &.underline {
      text-decoration: underline;
    }
}
a.text_color{
  color: $base-text-color;
  &:hover{
    color: $link-hover-color;
  }
}
a:focus{
  color: $base-text-color;
}

a,
.social_wrap{
    transition: color 200ms ease-in-out, background-color 200ms ease-in-out, border-color 200ms ease-in-out;
    -moz-transition: color 200ms ease-in-out, background-color 200ms ease-in-out, border-color 200ms ease-in-out;
    -ms-transition: color 200ms ease-in-out, background-color 200ms ease-in-out, border-color 200ms ease-in-out;
    -webkit-transition: color 200ms ease-in-out, background-color 200ms ease-in-out, border-color 200ms ease-in-out;
    -o-transition: color 200ms ease-in-out, background-color 200ms ease-in-out, border-color 200ms ease-in-out;
}

a:active,a:hover{
  outline: 0;
}
a:focus,:focus{ 
  outline: none!important;
  text-decoration: none;
}
.dropdown-item:focus,
.dropdown-item:hover {
  background: none;
}
.facet-label {
  &.active,
  &:hover {
    .custom-checkbox span.color {
      border-color: $gray-darker;
    }
  }
}
.close{
  opacity: 1;
  text-shadow: none;
  &:hover,&:focus{
    opacity: 1;
  }
}

.card {
  // @include box-shadow;
  @include border-radius(0);
  border-color: $gray-border-color;
}
.card-header{
  font-size: rem(14px);
  border-color: $gray-border-color;
  color: $link-color;
}
.card-footer{
  background: transparent;
  border-color: $gray-border-color;
}
.card{
  &.card_trans{
    background: transparent;
    .card-header{
      background: transparent;
    }
  }
  &.card_noborder{
    border: none;
  }
}
.table-bordered thead th, .table-bordered thead td{
  border-bottom-width: 1px; //2px may cause misunderstanding
}
.table-bordered, .table-bordered thead th, .table-bordered th, .table-bordered td{
  border-color: $base-border-color;
}
.list-group-item{
  background: transparent;
  border-color: $gray-border-color;
}
.form-control, .input-group-addon, .input-group-btn > .btn{
  font-size: rem(13px);
}
.form-control-label {
}
.form-control {
  border-color: $gray-border-color;
  padding: 6px;
  border-radius: 0;
  //padding: rem(6px);
  &:focus {
    border-color: $input-border-focus;
    outline: 0;
    color: $input-border-focus;
  }
}
.form-control::placeholder {color: #dadada;}
input.form-control{
  height: 34px;
  line-height: normal;
}
.input-group {
  .input-group-btn > .btn {
    //padding: rem(6px) rem(12px);
  }
}
.input-group-btn > .btn{
  font-size: 0.75rem;
}
.form-group-small{
  .form-control, .input-group{
    max-width: 360px;
  }
}
.form-control-select {
  height: rem(42px);
  -moz-appearance: none;
  -webkit-appearance: none;
  background: $gray-light url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAwCAQAAAD9CzEMAAAAPklEQVR4Ae3TwREAEBQD0V/6do4SXPZg7EsBhsQ8IEmSMOsiuEfg3gL3oXC7wK0bd1G4o8X9F4yIkyQfSrIByQBjp7QuND8AAAAASUVORK5CYII=") no-repeat scroll right rem(8px) center / rem(20px) rem(20px);
  padding-right: rem(32px);
}
.form-control-valign {
  padding-top: rem(8px);
}
.form-control-comment {
  font-size: rem(14px);
  padding-top: rem(8px);
  color: $gray-dark;
}
.form-control-submit {
  &.disabled {
    background: $brand-info;
    color: white;
  }
}
.form-group {
  &.has-error {
    input,
    select {
      border-color: $input-border-focus;
      color: $input-border-focus;
    }
    .help-block {
      color: $brand-danger;
    }
  }
}
/*This is nice, but a input should not count as invalid if users have not filled it.
input:invalid{
  border-color: $input-border-focus;
  color: $input-border-focus;
} */
.btn{
  border-radius: 0;
  font-size: rem(12px);
  height: 34px;
  line-height: 32px;
  padding: 0 rem(16px);  
  cursor: pointer;
  &:focus, &.focus{
    box-shadow: none;
  }
  i{
    margin-left: 2px;
    margin-right: 4px;
    font-size: rem(18px);
    vertical-align: middle;
    /* &::before{
      vertical-align: middle;
    } */
  }
  span{
    vertical-align: middle;
  }
}
.add-to-cart .btn i{
  font-size: rem(20px);
}
.btn-less-padding{
    padding-left: rem(8px);
    padding-right: rem(8px);
  }
.btn-more-padding{
    padding-left: rem(50px);
    padding-right: rem(50px);
  }
.btn-default{
  @include st-button($link-color,$white);
  border-width: 2px;
  line-height: 28px;
  box-shadow: inset 0 0 0 0 $link-color;
  /* .hover_fly_btn_inner{20170715 removed .style_content line-height setting
    line-height: 24px;//for products on cms page//should also be 280, set it to 26 to middle button text
  } */
  &.btn-large{
    line-height: 36px;
  }
}
.btn-white{
  @include st-button($white,$link-color);
  border-width: 2px;
  line-height: 28px;
  box-shadow: inset 0 0 0 0 $white;
}
.btn-primary{
  &:hover, &:focus, &:active, &.active{
    color: #fff;
    background-color: #000;
    border-color: #000;
  }
}
.btn-white, .btn-default, .btn-touchspin{ //otherwise btn-touchspin would fade out slowly when mouse over out
  -webkit-transition: color 300ms ease,background 300ms ease,border-color 300ms ease,box-shadow 300ms ease;
  transition: color 300ms ease,background 300ms ease,border-color 300ms ease,box-shadow 300ms ease;
}
.btn-link{
  padding: 0;
}
.btn-large{
  height: 40px;
  line-height: 38px;
  font-size: rem(14px);
}
.btn_inline{
  margin-right: 10px;
  margin-bottom: 6px;
  display: inline-block;
  i{
    margin-left: 2px;
    margin-right: 4px;
    font-size: rem(14px);
  }
}
.product_page_container{
  .pro_right_item{
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 6px;
  }
}
.round_item{

}


.input-group-with-border{
  border: 1px solid $gray-border-color;//base_border_color
  background: white;
  &.focus{
    border-color: #666666;
  }
  .form-control{
    border: none;
  }
  .btn{
    background: transparent;
    border: none;
  }
  .input-group-btn .btn{
    margin-left: 0;
  }
}

.bootstrap-touchspin {
  .form-control{
    padding: 0 2px;
    text-align: center;
    border-color: $gray-border-color;
  }
  .btn-touchspin {
    width: 24px;
    text-align: center;
    padding: 0;
    background-color: white;
    border-color: $gray-border-color;
    &:hover {
      background-color: $base-background;
    }
    i {
      margin: 0;
      font-size: rem(16px);
    }
  }
  .input-group-btn-vertical {
    .form-control{
      text-align: left;
    }
    .btn-touchspin{
      height: 18px;
      line-height: 16px;
      width: 17px;
      padding: 0;
      i{
        position: static;
        font-size: rem(12px);
      }
    }
    .bootstrap-touchspin-up {
      border-radius: 0;
    }
    .bootstrap-touchspin-down {
      border-radius: 0;
    }
  }
}

.radio-inline{
  margin-right: rem(16px);
}
.custom-radio {
  display: inline-block;
  position: relative;
  width: 16px;
  height: 16px;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 50%;
  border: $gray 2px solid;
  background: white;
  input[type="radio"] {
    opacity: 0;
    cursor: pointer;
  }
  input[type="radio"]:checked + span {
    display: block;
    background-color: $brand-primary;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    position: absolute;
    left: rem(2px);
    top: rem(2px);
  }
}
.checkbox-inline{
}
.custom-input-box {
  margin-right: 4px;
  position: relative;
  cursor: pointer;
  &:hover{
    .custom-input-item{
      &.custom-input-checkbox, &.custom-input-radio{
        background: $gray-background;
      }
    }
  }
  .custom-input {
    opacity: 0;
    position: absolute;
  }
  .custom-input-item {
    display: inline-block;
    width: rem(15px);
    height: rem(15px);
    line-height: rem(13px);
    vertical-align: middle;
    text-align: center;
    border: 1px $gray-border-color solid;
    background: $almost-white;
    &.custom-input-radio{
      @include border-radius(20px);
    }
    &.custom-input-color {
      background-size: contain;
      width: rem(15px);
      height: rem(15px);
      line-height: rem(13px);
      &:hover{
        border-color: $link-hover-color;
      }
    }
    i{
      display: none;
      font-size: rem(12px);
      color: $link-color;
    }
    .checkbox-checked {
      .facet-label.active &{
        display: block;
      }
    }
    .facet-label.loading &{
      .animate-spin{
        display: block;
      }
      .checkbox-checked {
        display: none;
      }
    }
  }
  input[type="checkbox"] {
    &:checked + .custom-input-item {
      .checkbox-checked {
        display: block;
        .facet-label.loading &{
          display: none;
        }
      }
    }
  }
}

.input-color,.input-radio, .st-input-loading {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
.input-color,.input-radio{
  cursor: pointer;
  opacity: 0;
  width: 100%;
  height: 100%;
}
.st-input-loading{
  background: rgba(255,255,255,0.8);
  z-index: 1;
  cursor: default;
  display: none;
  text-align: center;
  overflow: hidden;
  .st-input-clicked &{
    display: block;
  }
  i{
    height: 20px;
    line-height: 20px;
    width: 20px;
    position: absolute;
    top: 50%;
    margin-top: -10px;
    left: 50%;
    margin-left: -10px;
  }
}

.input-container {
  position: relative;
}

.input-color,
.input-radio {
  &:checked + span,
  &:hover + span {
    border: 2px solid $highlight-color;
  }
}
.input-radio {
  &:checked + span,
  &:hover + span {
    padding: 0px 8px;
    line-height: rem(26px);
  }
}
.radio-label {
  background: white;
  display: inline-block;
  height: rem(30px);
  line-height: rem(28px);
  padding: 0 9px;
  border: 1px solid $gray-border-color;
}
.color{
  display: inline-block;
  border: 1px solid $gray-border-color;
  background-size: contain;
  width: rem(20px);
  height: rem(20px);
}
.product-variants{
  .color{
    width: rem(30px);
    height: rem(30px);
    &.texture{
      width: rem(40px);
      height: rem(40px);
    }
  }
}
.variant-links{
  .color{
    margin: 0 rem(4px) rem(6px) 0;
    width: 15px;
    height: 15px;
    line-height: 15px;
  }
}
/* .done {
  color: $brand-success;
  display: inline-block;
  padding: 0 rem(13px);
  margin-right: rem(25px);
} */
.thumb-mask {
  > .mask {
    position: relative;
    width: rem(55px);
    height: rem(55px);
    overflow: hidden;
    border: $gray-light 1px solid;
    margin: $small-space 0;
    img {
      width: 55px;
      height: 55px;
    }
  }
}
.definition-list {
  dl {
    @include display(flex);
    @include flex-wrap(wrap);
    dt {
      font-weight: normal;
    }
    dd,
    dt {
      @include flex(0 0 45%);
      background: $gray-light;
      padding: rem(10px);
      margin: rem(2px);
      &:nth-of-type(even) {
        background: $gray-lighter;
      }
    }
  }
}
.pagination{
  margin: 0;
}
.bordered{
  border: 1px solid $base-border-color;
}


