.facet-dropdown {
  color: $gray-dark;
  padding-left: 0;
  padding-right: 0;
  background: $gray-light;
  border: 3px solid transparent;
  box-sizing: border-box;
  box-shadow: 1px 1px 1px 1px $gray-light;
  &.open {
    border: 0;
    > .select-title {
      border: 3px solid $brand-primary;
      background: $gray-lighter;
    }
  }
  .select-title {
    display: inline-flex;
    align-items: center;
    margin-left: 0;
    width: 100%;
    color: $gray-darker;
    background: $gray-light;
    padding: $small-space $small-space $small-space $medium-space;
    cursor: pointer;
    > i {
      margin-left: auto;
    }
  }
  .select-list {
    display: block;
    color: $gray-darker;
    background: $gray-lighter;
    padding: $small-space $medium-space;
    &:hover {
      background: $brand-primary;
      color: white;
      text-decoration: none;
    }
  }
  .dropdown-menu {
    padding: 0;
    margin-top: 3px;
    left: auto;
    width: 100%;
    background: $gray-lighter;
    border: none;
    box-shadow: 1px 1px 1px 1px $gray-light;
  }
}
#search_filters,
#search_filters_brands,
#search_filters_suppliers {
  .facet {
    padding-top: 0;
    .facet-label {
      margin-bottom: rem(5px);
    }
  }
}
.column_filter{
  #search_filters {
    .facet {
      .collapse {
        display: block;
      }
    }
  }
  .facet_filter_item_li{
    padding: 4px 0;
  }
}
.horizontal_filters{
  .facet{
    border-bottom: 1px dashed $base-border-color;
    &:last-child{
      border-bottom: none;
    }
  }
  .facet-title{
    min-width: 88px;
    margin: 6px 6px 6px 0;
  }
}
.horizontal_filters_dropdown{
  #search_filters{
    position: relative;//to make drop down filters be full screen
  }
  .dropdown_wrap{
    position: static;
  }
  .dropdown_list{
    right: 0;//to make drop down filters be full screen
    padding: 6px;
    border: 1px solid $base-border-color;
    &:after, &:before{
      display: none;
    }
  }
  .open .dropdown_list{
    background: #fff;
  }
  .facet{
    display:inline-block;
    margin-right: 6px;
  }
  .facet-title-mobile{
    display: none;
    padding: 6px 0;
  }
}
.horizontal_filters, .horizontal_filters_dropdown{
  .facet_filter_item_li, .facet_filter_item_form{
    display: inline-block;
    margin: 6px 30px 6px 0;
  }
}
//for list filters on center column on desktop
.facet .collapse{
  display: block;
}
