/*** Responsive part ***/
@include media-breakpoint-down(sm) {
  #category {
    #left-column {
      width: 100%;
      #search_filters_wrapper {
        margin-left: -30px;
        margin-right: -30px;
      }
      #search_filter_controls {
        text-align: center;
        button {
          margin: 0 rem(8px);
        }
        margin-bottom: rem(16px);
      }
      #search_filters {
        margin-bottom: 0;
        box-shadow: none;
        padding: 0;
        border-top: 1px solid $gray-lighter;
        .facet {
          .title {
            cursor: pointer;
            .collapse-icons .remove {
              display: none;
            }
          }
          .title[aria-expanded="true"] {
            .collapse-icons {
              .add {
                display: none;
              }
              .remove {
                display: block;
              }
            }
          }
          padding-top: 0;
          border-bottom: 1px solid $gray-lighter;
          .facet-title {
            color: $gray-darker;
            text-transform: uppercase;
          }
          .h6 {
            margin-bottom: 0;
            padding: rem(10px);
            display: inline-block;
          }
          .navbar-toggler {
            display: inline-block;
            padding: rem(10px) rem(10px) 0 0;
          }
          .collapse {
            display: none;
            &.show {
              display: block;
            }
          }
          .facet-label {
            a {
              margin-top: 0;
            }
          }
          ul {
            margin-bottom: 0;
            li {
              border-top: 1px solid $gray-lighter;
              padding: rem(10px);
            }
          }
        }
      }
    }
    #content-wrapper {
      width: 100%;
    }
    #search_filter_toggler {
      width: 100%;
    }
  }
  
  .products-selection {
    h1 {
      padding-top: 0;
      text-align: center;
      margin-bottom: rem(16px);
    }
    .showing {
      padding-top: rem(16px);
    }
  }
  #prices-drop #content-wrapper,
  #new-products #content-wrapper,
  #best-sales #content-wrapper {
    width: 100%;
  }
}

@include media-breakpoint-down(xs) {
  .products-selection {
    .filter-button {
      padding-left: 0;
    }
  }
  #category {
    #left-column {
      #search_filters_wrapper {
        margin-left: -15px;
        margin-right: -15px;
      }
    }
  }
}

.product_list.grid {
  .product_list_item {
    border-top: 1px solid $base-border-color;
    padding: 24px 7px 24px 8px;
    body#index &{
      border-top: none;
    }
  }
}
.products.product_list.grid {
  margin-left:-7px;
  margin-right:-8px;
}

/*******************************************************
Product list(List) Styles 
********************************************************/
.product_list.list {
  .product_list_item{
    clear: both;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #f5f5f5;
    &:last-child{
      border-bottom: none;
    }
    &:first-child{
      padding-top: 0;
    }
  }
  .pro_second_box{
    padding-top:8px; //to make spaces between product name and the top border
  }
  .block_blog{
    .pro_second_box{
      &.pro_block_align_0{
        text-align: center;
      }
      &.pro_block_align_1{
        text-align: left;
      }
    }
    .blog_lr{
      .pro_second_box{
        &.pro_block_align_0{
          text-align: left;
        }
      }
    }
  }

  .blog_lr{
    .pro_first_box{
      float: left;
      max-width: 360px;
    }
    .pro_second_box{
      margin-left: 380px;    
    }
  }
  .ajax_block_product {
    .yotpo{
      clear: none;
      .yotpo-clr{
        clear: none;
      }
    }
    .hover_fly{
      display: none;
    }
    .pro_first_box{
      float: left;
      max-width: 280px;
    }
    .pro_second_box{
      margin-left: 300px;    
    }
    .s_title_block{
      height: auto;
      font-size: rem(16px);
      margin-bottom: rem(16px);
    }
    .act_box{
      padding-top: 1em;
    }
  }
  .big_page_next_wrap{
    display: none;
  }
}

#big_page_next{
  a{
    &:hover{
      color: #fff;
      .text_table_wrap{
        background: $dark-background;
      }
    }
    .text_table_wrap{
      background: $base-background;
      .text_td{
        font-size: 2em;
      }
    }
  }
}
