@mixin search-box {
  form {
    position: relative;
    input[type=text] {
      &:focus {
        outline: 3px $brand-primary solid;
        color: $gray-darker;
        background: white;
        + button .search {
          color: $brand-primary;
        }
      }
      border: none;
      padding: 10px;
      min-width: 255px;
      background: $gray-light;
      color: $gray;
      @include placeholder {
        color: rgba($gray, 0.5);
        text-indent: rem(10px);
      }
    }
    button[type=submit] {
      position: absolute;
      background: none;
      border: none;
      bottom: rem(5px);
      right: rem(2px);
      color: $gray-dark;
      .search:hover {
        color: $brand-primary;
      }
    }
  }
}

@mixin box-shadow {
  box-shadow: 2px 2px 11px 0px rgba(0, 0, 0, 0.1);
}
@mixin st-button($color, $active-color) {
  color: $color;
  background-image: none;
  background-color: transparent;
  border-color: $color;

  &:hover {
    color: $active-color;
    background-color: $color;
        border-color: $color;
  }

  &:focus,
  &.focus {
    color: $active-color;
    background-color: $color;
        border-color: $color;
  }

  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $active-color;
    background-color: $color;
        border-color: $color;

    &:hover,
    &:focus,
    &.focus {
      color: $active-color;
      background-color: $color;
          border-color: $color;
    }
  }

  &.disabled,
  &:disabled {
    &:focus,
    &.focus {
      border-color: $color;
    }
    &:hover {
      border-color: $color;
    }
  }
}


