.quickview {
  .modal-dialog {
    max-width: rem(992px);
  }
  .modal-content {
    min-height: rem(450px);
    -webkit-box-shadow: 0 0px 20px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 0 0px 20px rgba(0, 0, 0, 0.5);
    box-shadow: 0 0px 20px rgba(0, 0, 0, 0.5); 
  }
  .modal-header {
    border: none;
    padding: $small-space;
  }
  .modal-body {
    min-height: rem(380px);
  }
  .modal-footer {
    border-top: 1px solid rgba($gray,.3);
  }
  .layer {
    display: none;
  }
  .product-cover img {
  }
  .images-container {
    
  }
  .social-sharing {
    margin-top: 0;
    margin-left: $extra-large-space;
  }
}



