/*** SOME GENERIC STYLES ***/
@mixin customer-area-base-box {
  @include box-shadow;
  background: white;
  padding: rem(16px);
}
@mixin customer-area-box {
  @include customer-area-base-box;
  font-size: $font-size-sm;
  color: $gray-dark;
}
.table-labeled {
  th {
    vertical-align: middle;
  }
  td {
    vertical-align: middle;
  }
}
.page-order {
  .table {
    margin-bottom: 0;
  }
  table {
    th {
      padding: rem(8px);
    }
    td {
      padding: rem(8px);
    }
    thead {
      th {
        text-align: center;
      }
    }
  }
}
#authentication {
  .tooltip.tooltip-bottom {
    padding: 0;
    margin: 0;
  }
  /* [data-action="show-password"]{
    background: $gray-dark;
  } */
}

/*** Most of the customer accpunt pages ***/

/*** Login page ***/
#create_account_block .form_content, #login_form_block .form_content {
    min-height: 200px;
}

.from_blcok .form_content_inner {
    padding: 0em rem(16px);
}

.from_blcok .form-footer {
    padding: 6px rem(16px) rem(16px) rem(16px);
}
.from_blcok{
  border: 1px solid $gray-border-color;
  &.has_social_login{
      min-height: 250px
  }
}

