.product-price {
  display: inline-block;
}
.product-description-short {
  
}

.product-actions {
  
}

.product-quantity {
  .add {
    display: inline-block;
    vertical-align: middle;
    &.add_full_width{
      display: block;
    }
  }
}
.product-discounts {
  > .product-discounts-title {
    font-weight: normal;
    font-size: $font-size-sm;
  }
  > .table-product-discounts {
    thead tr th {
      width: 33%;
      padding: $small-space $medium-space;
      background: white;
      border: rem(5px) $gray-light solid;
      text-align: center;
    }
    tbody tr {
      background: $gray-lighter;
      &:nth-of-type(even) {
        background: white;
      }
      td {
        padding: $small-space $medium-space;
        text-align: center;
        border: rem(5px) $gray-light solid;
      }
    }
  }
}
.product-prices {
  .tax-shipping-delivery-label {
    font-size: rem(13px);
    color: $gray-dark;
  }
}


.product-unit-price {
  font-size: $font-size-xs;
  margin-bottom: 0;
}

  .product-available {
    color: $brand-success;
  }
  .product-unavailable {
    color: $brand-warning;
  }
  .product-last-items {
    color: $brand-warning;
  }

#product-details {
  .label {
    font-size: $font-size-base;
    color: $gray-darker;
    font-weight: bold;
  }
  .product-reference{
    display: none;
  }
}
.product-features {
  margin-top: $medium-space;
  dl.data-sheet {
    border-bottom: 1px dashed $base-border-color;
    padding: 6px 0;
    margin-bottom: 0;
    dt.name {
      text-transform: capitalize;
      min-width: 200px;
      font-weight: bold;
    }
    dd.value{
      margin-bottom: 0;
    }
  }
}
.product-variants {
  .product-variants-item {
    margin-bottom: 10px;
    select {
      border: 1px solid $gray-light;
      height: rem(30px);
      // width: rem(60px);
      width: 100%;
    }
    ul {
      margin-bottom: 0;
      li{
      margin-right: 6px;
      }
    }
    .color {
      margin-left: 0;
      margin-top: 0;
    }
  }
  .control-label{
    margin-bottom: 4px;
    display: block;
  }
}
.product-flags {
  position: absolute;
  top: 0;
  width: 100%;
}
li.product-flag {
    display: block;
    position: absolute;
    background: $brand-primary;
    font-weight: 700;
    padding: rem(5px) rem(7px);
    text-transform: uppercase;
    color: white;
    margin-top: $small-space;
    margin-left: rem(-5px);
    z-index: 2;
    &.online-only{
      top: rem(400px);
      right: 0;
      font-size: $font-size-xs;
      &::before{
        content:"\E30A";
        font-family: 'Material Icons';
        vertical-align: middle;
        margin: rem(5px);
      }
    }
    &.discount {
      display: none;
    }
    &.on-sale {
      background: $brand-secondary;
      width: 100%;
      text-align: center;
      margin: 0;
      left:0;
      top:0;
    }
}

.product-customization {
  .product-message {
    background: $gray-light;
    border: none;
    width: 100%;
    height: rem(50px);
    resize: none;
    padding: rem(10px);
  }
  .file-input {
    width: 100%;
    opacity: 0;
    left: 0;
    z-index: 1;
    cursor: pointer;
    height: rem(34px);
    overflow: hidden;
    position: absolute;
  }
  .custom-file {
    position: relative;
    background: $gray-light;
    width: 100%;
    height: rem(34px);
    line-height: rem(34px);
    text-indent: rem(10px);
    display: block;
    button {
      z-index: 0;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
}
