.pro_gallery_thumb_box{
  cursor: pointer;
  display: inline-block;
  .clicked_thumb &{
    border-color: $base-text-color;
  }
}
.hightlight_curr_thumbs{
  .pro_gallery_thumb{
    opacity: 0.4;
  }
  .clicked_thumb, .curr_combination_thumb{
    .pro_gallery_thumb{
      opacity: 1;
    }
  }
}


