.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  /* Fix of Webkit flickering */
  z-index: 1;
}
.swiper-container-no-flexbox .swiper-slide {
  float: left;
}
.swiper-container-vertical > .swiper-wrapper {
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -ms-flex-direction: column;
  -webkit-flex-direction: column;
  flex-direction: column;
}
.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  -o-transition-property: -o-transform;
  -ms-transition-property: -ms-transform;
  transition-property: transform;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
.swiper-container-android .swiper-slide,
.swiper-wrapper {
  -webkit-transform: translate3d(0px, 0, 0);
  -moz-transform: translate3d(0px, 0, 0);
  -o-transform: translate(0px, 0px);
  -ms-transform: translate3d(0px, 0, 0);
  transform: translate3d(0px, 0, 0);
}
.swiper-container-multirow > .swiper-wrapper {
  -webkit-box-lines: multiple;
  -moz-box-lines: multiple;
  -ms-flex-wrap: wrap;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
}
.swiper-container-free-mode > .swiper-wrapper {
  -webkit-transition-timing-function: ease-out;
  -moz-transition-timing-function: ease-out;
  -ms-transition-timing-function: ease-out;
  -o-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
  margin: 0 auto;
}
.swiper-slide {
  -webkit-flex-shrink: 0;
  -ms-flex: 0 0 auto;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
}
/* Auto Height */
.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto;
}
.swiper-container-autoheight .swiper-wrapper {
  -webkit-box-align: start;
  -ms-flex-align: start;
  -webkit-align-items: flex-start;
  align-items: flex-start;
  -webkit-transition-property: -webkit-transform, height;
  -moz-transition-property: -moz-transform;
  -o-transition-property: -o-transform;
  -ms-transition-property: -ms-transform;
  transition-property: transform, height;
}
/* a11y */
.swiper-container .swiper-notification {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  opacity: 0;
  z-index: -1000;
}
/* IE10 Windows Phone 8 Fixes */
.swiper-wp8-horizontal {
  -ms-touch-action: pan-y;
  touch-action: pan-y;
}
.swiper-wp8-vertical {
  -ms-touch-action: pan-x;
  touch-action: pan-x;
}
/* Arrows */
.swiper-button{
  cursor: pointer;
}
.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  cursor: auto;
  // pointer-events: none; //if click on a disabled button, link below will be clicked
}
.swiper-button-prev{
  .fto-left-open-3, .slider_arrow_left{
    display: block;
    .is_rtl &{
      display: none;
    }
  }
  .fto-right-open-3, .slider_arrow_right{
    display: none;
    .is_rtl &{
      display: block;
    }
  }
}
.swiper-button-next{
  .fto-right-open-3, .slider_arrow_right{
    display: block;
    .is_rtl &{
      display: none;
    }
  }
  .fto-left-open-3, .slider_arrow_left{
    display: none;
    .is_rtl &{
      display: block;
    }
  }
}
.swiper-button-lr{
  overflow: hidden;
      .swiper-button {
        position: absolute;
        overflow: hidden;
        text-align: center;
        z-index: 5;
        cursor: pointer;
        &.swiper-button-disabled{
          cursor: default;
        }
        i{
          height: 46px;
          line-height: 46px;
          width: 46px;
          text-align: center;
          font-size: 1.5em;
        }
      }
    .swiper-button-prev,.swiper-button-next{
        top: 0;
        bottom: 0;
        height: 100%;
        width: 46px;
        i{
          position: absolute;
            top: 50%;
          margin-top: -23px;
        }
    }
    .swiper-button-top,.swiper-button-bottom{
        right: 0;
        left: 0;
        width: 100%;
        height: 26px;
        line-height: 26px;
        i{
          height: 26px;
          line-height: 26px;
          position: absolute;
          left: 50%;
          margin-left: -23px;
        }
    }

      .swiper-button-prev{
        left: -100px;
        -webkit-transition: left 300ms ease;
        transition: left 300ms ease;
        .is_rtl &{
          left: auto;
          right: -100px;
        }
      }
      .swiper-button-next{
        right: -100px;
        -webkit-transition: right 300ms ease;
        transition: right 300ms ease;
        .is_rtl &{
          right: auto;
          left: -100px;
        }
      }
      .swiper-button-top{
        top: -100px;
        -webkit-transition: top 300ms ease;
        transition: top 300ms ease;
        .slider_arrow_top{
          display: block;
        }
        .slider_arrow_bottom{
          display: none;
        }
      }
      .swiper-button-bottom{
        bottom: -100px;
        -webkit-transition: bottom 300ms ease;
        transition: bottom 300ms ease;
        .slider_arrow_bottom{
          display: block;
        }
        .slider_arrow_top{
          display: none;
        }
      }
  &:hover,&.swiper-navigation_visible{
        > .swiper-button-prev{
          left: 0;
          .is_rtl &{
            left: auto;
            right: 0;
          }
        }
        > .swiper-button-next{
          right: 0;
          .is_rtl &{
            right: auto;
            left: 0;
          }
        }
        .swiper-button-top{
            top: 0;
        }
        .swiper-button-bottom{
            bottom: 0;
        }
  }
  &.swiper-navigation-full, &.swiper-navigation-rectangle, &.swiper-navigation-circle{
    .swiper-button {
      color: $light-text-color;
      background: rgba(0,0,0,0.4);
      &.swiper-button-disabled{
        color: #777777;
      }
      .products_slider &{
        background: #cccccc;
      }
      &:hover{
        background: rgba(0,0,0,0.8);
        .products_slider &{
          background: #777777;
        }
      }
      &.swiper-button-disabled:hover{
        background: rgba(68,68,68,0.4);
        .products_slider &{
          background: #cccccc;
        }
      }
    }
  }
  &.swiper-navigation-rectangle, &.swiper-navigation-circle, &.swiper-navigation-arrow{
    
    .swiper-button-prev,.swiper-button-next{
          bottom: auto;
          height: auto;
          top: 50%;
          margin-top: -34px;
          width: auto;
          padding: 0;
        i{
            position: static;
            height: 68px;
            line-height: 66px;
            width: 36px;
            margin-top: 0;
            
        }
        &.swiper-button-outer{
          .products_slider &{
            top: 40%;
          }
          .brands_slider .products_slider &{
            top: 50%;
          }
        }
    }
    .swiper-button-top,.swiper-button-bottom{
        width: auto;
        height: auto;
        line-height: auto;
        left: 50%;
        right: auto;
        margin-left: -24px;
        i{
            position: static;
            height: 26px;
            line-height: 26px;
            width: 48px;
            margin-left: 0;
        }
    }
  }
  &.swiper-navigation-rectangle{
        .swiper-button {
          @include border-radius(4px);
        }
    &.swiper-small-button{
      .swiper-button {
        margin-top: -19px;
        i{
          width: 22px;
          height: 38px;
          line-height: 36px;
          font-size: rem(16px);
        }
      }
    }
  }
  &.swiper-navigation-circle, &.swiper-navigation-arrow{
    &.swiper-small-button{
      .swiper-button {
        i{
          width: 30px;
          height: 30px;
          line-height: 30px;
          font-size: rem(16px);
        }
      }
    }
    .swiper-button-prev,.swiper-button-next{
        margin-top: -20px;
        i{
            height: 40px;
            line-height: 40px;
            width: 40px;
        }
    }
    .swiper-button-top,.swiper-button-bottom{
        margin-left: -15px;
        i{
            height: 30px;
            line-height: 30px;
            width: 30px;
        }
    }
    &:hover, &.swiper-navigation_visible{
          > .swiper-button-prev{
            left: 10px;
            .is_rtl &{
              left: auto;
              right: 10px;
            }
          }
          > .swiper-button-next{
            right: 10px;
            .is_rtl &{
              right: auto;
              left: 10px;
            }
          }
    }
  }
  &.swiper-navigation-circle{
    .swiper-button {
      @include border-radius(100px);
      overflow: hidden;
      i{
        @include border-radius(100px);
      }
    }
  }
  &.swiper-navigation-arrow{
    .swiper-button {
      @include border-radius(4px);
      color: $light-text-color;
      i{
        font-size: rem(30px);
      }
      .fto-left-open-3:before { content: '\e8c3'; }
      .fto-right-open-3:before { content: '\e8c6'; }
    }
  }
}
.swiper-button-tr{
  .swiper-button{
    height: 24px;
    line-height: 24px;
    width: 24px;
    text-align: center;
    border-radius: 0px;
    color: #444444;
    display: inline-block;
    margin-right: 2px;
    &.swiper-button-disabled{
      color: #999999;
    }
    &:hover{
      background-color: #f2f2f2;
    }
    &.swiper-button-disabled:hover{
      background-color: transparent;
    }
  }
} 
